.Auto{
    flex: 0 0 23%;
    margin: 0 1% 25px;
    &__title{
        text-align: center;
        font-size: 24px;
        line-height: 1.5;
        color: darkred;
    }
    &__description{
        text-align: justify;
    }
    &__price{
        text-align: center;
        font-size: 30px;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    &__button{
        display: inline-block;
        width: 100%;
        padding: 8px 15px;
        text-align: center;
        background-color: #08c;
        color: #fff;
        outline: none;
        border: none;
        border-radius: 5px;
    }
    &__img img{
        width: 100%;
        min-width: 180px;
    }
}